<template>
  <div class="home">
    <Swiper :key="bannerArr.length">
      <swiper-slide class="swiper-slide" v-for="(item,index) in bannerArr" :key="index">
        <img :src="item.picUrl" />
      </swiper-slide>
    </Swiper>
  </div>
</template>

<script>
  import Swiper from "@/components/Swiper"
  export default {
    data() {
      return {
        name: 'home',
        bannerArr: []
      }
    },
    components: {
      Swiper
    },
    mounted() {
      this.$axios.get('/static/api/banner.json').then(res => {
        this.bannerArr = res.data;
      })
    },
    methods: {

    }
  }
</script>

<style scoped>
  .swiper-slide img {
    width: 100%;
  }
</style>



