<template>
  <div class="tabbar">
    <ul>
      <router-link to="/home" tag="li" active-class="tab_on">
        <span class="iconfont iconhome"></span>
      </router-link>
      <router-link to="/about" tag="li" active-class="tab_on">
        <span class="iconfont iconaboutus"></span>
      </router-link>
      <router-link to="/my" tag="li" active-class="tab_on">
        <span class="iconfont iconmy"></span>
      </router-link>
    </ul>
  </div>
</template>

<script>
  export default {
    data() {
      return {

      }
    }
  }
</script>

<style scoped>
  .tabbar ul {
    width: 100%;
    height: 49px;
    border-top: 1px solid #eeeeee;
    overflow: hidden;
    position: fixed;
    bottom: 0;
  }

  .tab_on {
    color: red;
  }

  li {
    width: 33.33%;
    height: 49px;
    line-height: 49px;
    text-align: center;
    float: left;
  }
</style>



