<template>
  <div class="login">
    <h3>用户登录</h3>
    <el-input placeholder="请输入用户名" v-model="user" clearable class="user"></el-input>
    <el-input placeholder="请输入密码" v-model="password" show-password class="password"></el-input>
    <el-row>
      <el-button type="primary" class="login_btn" @click='login'>登录</el-button>
    </el-row>
  </div>
</template>

<script>
  // import bus from '@/bus'
  export default {
    data() {
      return {
        user: 'zhang',
        password: '123456'
      }
    },
    beforeMount() {
      // bus.$emit('tabShow', false)
      // this.$store.state.tabShow = false
      this.$store.commit('tabShow', false)
    },
    methods: {
      login() {
        sessionStorage.setItem('user', this.user);
        this.$router.push('/my');
      }
    },
    destroyed() {
      // bus.$emit('tabShow', true)
      // this.$store.state.tabShow = true
      this.$store.commit('tabShow', true)
    }
  }
</script>

<style scoped>
  .login {
    padding: 20px 5%;
  }

  .user {
    margin: 20px auto;
  }

  .login_btn {
    margin-top: 30px;
    width: 100%;
  }
</style>



