import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import About from '@/views/About'
import My from '@/views/My'
import Login from '@/views/Login'

Vue.use(Router)

const router = new Router({
  routes: [{
      path: '/home',
      component: Home
    },
    {
      path: '/about',
      component: About
    },
    {
      path: '/my',
      component: My
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '',
      redirect: '/home'
    }
  ],
  // mode: 'history'
})

router.beforeEach((to, from, next) => {
  if (to.path === '/my' && sessionStorage.getItem('user') == null) {
    next('/login');
  } else {
    next();
  }
})

export default router



